<template>
  <!-- 组合文章详情页组件 -->
  <Layout>
    <!-- vue-router 路由匹配到的组件会渲染到这里 -->
    <div ref="mainContainer" class="main-container" v-loading="isLoading">
      <BlogDetail :blog="data" v-if="data" />
      <BlogComment v-if="!isLoading" />
    </div>
    <template #right>
      <div class="right-container" v-loading="isLoading">
        <BlogTOC :toc="data.toc" v-if="data" />
      </div>
    </template>
  </Layout>
</template>

<script>
import fetchData from "@/mixins/fetchData";
import { getBlog } from "@/api/blog";
import Layout from "@/components/Layout";
import BlogDetail from "./components/BlogDetail";
import BlogTOC from "./components/BlogTOC";
import BlogComment from "./components/BlogComment";
import mainScroll from "@/mixins/mainScroll.js";
import { titleController } from "@/utils/index"; //导入写好的设置标题的工具函数

export default {
  components: {
    //注册各种组件
    Layout,
    BlogDetail,
    BlogTOC,
    BlogComment,
  },
  mixins: [fetchData(null), mainScroll("mainContainer")],
  methods: {
    // 请求数据函数，传入id值为地址栏id值
    async fetchData() {
      const resp = await getBlog(this.$route.params.id);
      if (!resp) {
        //判断如果文章不存在的化跳转到404
        this.$router.push({ name: "NotFound" });
      }
      titleController.setRouteTitle(resp.title);
      return resp;
    },
  },

  updated() {
    // 下面的代码意思是在请求数据后设置先把哈希值变成空
    // 等一段时间差不多数据请求了,在数组哈希值为之前的哈希值就可以实现进入到之前选中的位置
    const hash = location.hash; //存储哈希值
    location.hash = ""; //设置哈希值为空
    setTimeout(() => {
      //设置一个计时器等多少秒后，再次把哈希值变回来
      location.hash = hash;
    }, 50);
  },
};
</script>

<style scoped lang="less">
.main-container {
  overflow-y: scroll;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
  width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
.right-container {
  width: 300px;
  height: 100%;
  overflow-y: scroll;
  box-sizing: border-box;
  position: relative;
  padding: 20px;
}
</style>
